.post-event {
    margin-top: 50px;
    width: 100%;
    border: solid 1px rgb(197, 197, 197);
    background-color: rgba(255, 255, 255);
    color: grey;
    border-radius: 10px;
}

.post-event-header {
    display: flex;
    border-radius: 10px;
}


.post-event-image {
    display: flex;
    width: 798px;
}

.post-event-header-title {
    font-size: 2rem;
}

.post-event-header-detail {
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.8);
}

.post-event-description {
    padding: 20px;
    height: 200px;
    background-color: red;
    color: white;
}

.post-event-buttons {
    padding: 20px;
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: flex-end;
    background-color: red;
    color: white;
    border-radius: 0 0 10px 10px;
}

.post-event-buttons button {
    padding: 10px;
}


.post-event-delete {
    margin-right: 20px;
    background-color: rgb(59, 59, 59);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.5s;
}

.post-event-delete:hover {
    background-color: rgb(0, 0, 0);
}

.interested {
    background-color: rgb(89, 147, 255);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.5s;
}

.interested:hover {
    background-color: rgba(55, 102, 255);
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .post-event {
        margin-top: 50px;
        width: 100%;
        border: solid 1px rgb(197, 197, 197);
        background-color: rgba(255, 255, 255);
        color: grey;
        border-radius: 0px;
    }
    
    .post-event-header {
        display: flex;
        border-radius: 0px;
    }
        
    .post-event-image {
        display: flex;
        width: 100%;
    }

    .post-event-header-title {
        font-size: 2rem;
    }

    .post-event-header-detail {
        display: flex;
        flex-direction: column;
        padding: 20px;
        background-color: rgba(255, 255, 255, 0.8);
    }

    .post-event-description {
        padding: 20px;
        height: 200px;
        background-color: red;
        color: white;
    }
    .post-event-buttons {
        padding: 20px;
        display: flex;
        position: relative;
        justify-content: center;
        align-items: flex-end;
        background-color: red;
        color: white;
        border-radius: 0px;
    }
  }