.register {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    /* background-color: blue; */
}

.register form {
    
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 30%;
/* background-color: red; */
}

.register form input{
    outline: none;
}



.register-button {

    border: none;
    background-color: red;
    color: white;
    cursor: pointer;
}

.return-login {

    border: none;
    background-color: rgb(0, 110, 255);
    color: white;
    cursor: pointer;
}


.register-element {
    padding: 10px;
    margin: 10px 0;
    width: 100%;
    border-radius: 5px;
}



@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .register-element {
        margin: 10px 0;
        width: 300%;
    }
    .label {
        width: 300%;
        margin-top: 10px;
    }
    .return-login {
        display: flex;
        position: relative;
        left: -100%;
    }
  }